import React, {Fragment, Component} from 'react'
import {withStyles} from '@material-ui/core/styles'
import Typography from '@material-ui/core/Typography'
import Grid from "@material-ui/core/Grid";
import {supportedBy} from '../../data'
import {knowledgePartner} from '../../data'

import {Element} from "react-scroll/modules";

const Styles = (theme) => ({
    container: {
        width:'85%',
        margin: 'auto',
        textAlign: 'left',
        padding: "10px 0"

    },
    sectionTitle: {
        fontSize: '18px',
        fontWeight: '800',
        color: '#2963B4',
        textAlign: 'left',
        padding: '15px 10px 5px 0px'

    },
    logo: {
        textAlign: 'left',
    },
    logoList: {
        listStyle: '',
        display: 'inline',
        margin: '15px',
    },
    img: {
        verticalAlign: 'middle',
        transition: 'filter .5s ease-in-out',
        WebkitFilter: 'grayscale(0%)',
        filter: 'grayscale(0%)',
        '&:hover': {
            WebkitFilter: 'grayscale(100%)', /* Ch 23+, Saf 6.0+, BB 10.0+ */
            filter: 'grayscale(100%)'
        }
    },
    [theme.breakpoints.down('md')]:{
        sectionTitle: {
            textAlign: 'center',

        },
        logo: {
            textAlign: 'center',
        },
        container: {
            width:'85%',
            margin: 'auto',
            textAlign: 'center',

        },
        img:{
            paddingButtom: '20px'


        },


    }


});

class Knowledge extends Component {
    logo = Array.prototype.sort.apply(supportedBy);

    render() {
        const {classes} = this.props
        return (
            <Fragment>
                <Element name="Host organization" className="element">
                    <Grid container spacing={32} className={classes.container}>
                        <Grid xs={12}  sm={12} md={8}>
                            <Typography variant='h4' className={classes.sectionTitle}>
                                Supported by
                            </Typography>
                                {this.logo.map((item, index) => (
                                        <img className={classes.img}
                                                                    src={process.env.PUBLIC_URL + item.url}
                                                                    alt=""/>
                                ))}
                        </Grid>
                        <Grid xs ={12} sm={12} md={4}>
                            <Typography variant='h4' className={classes.sectionTitle}>
                                Knowledge Partner
                            </Typography>
                                {knowledgePartner.map((item, index) => (
                                        <img className={classes.img}
                                                                    src={process.env.PUBLIC_URL + item.url}
                                                                    alt=""/>
                                ))}
                        </Grid>
                    </Grid>
                </Element>
            </Fragment>
        );
    }

}

export default withStyles(Styles)(Knowledge)